<template>
  <div class="home">
    <v-container>
      <v-spacer></v-spacer>

      <v-card dark shaped color="#182C61" elevation="10" class="mt-6 mb-1 px-5">
        <v-row>
          <v-col cols="md-2">
            <v-img
              src="@/assets/fgrlogo.png"
              contain
              max-height="100"
              position="left"
            >
            </v-img>
          </v-col>
          <v-col class="text-right" align-self="center">
            <strong class="text-md-h6"> Focus Group Updatito </strong> <br />
            <i class="subtitle-1"> Session 41 </i>
          </v-col>
        </v-row>
      </v-card>
      <v-form>
        <v-card color="#3B3B98" dark shaped elevation="2">
          <v-pagination
            dark
            circle
            v-model="page"
            class="py-5 my-3"
            :length="pageLength"
            @next="breakjson"
            @previous="breakjson"
            @input="breakjson"
          ></v-pagination>
        </v-card>
        <v-card class="" color="#182C61" dark shaped>
          <v-toolbar dense color="#6D214F" class="caption font-weight-thin">
            <em>
              Scroll through the songs sar ^ or use the buttons arrow buttons
            </em>
          </v-toolbar>

          <v-row justify="center">
            <v-col cols="8" justify-center align-self="center">
              <v-text-field
                name="songName"
                label="Song Name"
                id="id"
                v-model="songName"
              ></v-text-field>
              <v-text-field
                name="artistName"
                label="Artist"
                id="id"
                v-model="artist"
              ></v-text-field>
              <v-row justify="center" class="mb-5 mt-5">
                <v-btn large color="#58B19F" light @click="sendesh">Send</v-btn>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-form>

      <div class="">
        <v-btn
          fab
          dark
          fixed
          bottom
          left
          color="#182C61"
          x-large
          class="mb-15"
          @click="floatbtn(-1)"
        >
          <v-icon> mdi-arrow-left </v-icon>
        </v-btn>
        <v-btn
          fab
          fixed
          bottom
          right
          dark
          color="#182C61"
          x-large
          class="mb-15"
          @click="floatbtn(+1)"
        >
          <v-icon> mdi-arrow-right </v-icon>
        </v-btn>
      </div>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import fgr from "@/assets/fgr41y.JSON";
export default {
  name: "Youtube",
  data() {
    return {
      page: 1,
      artist: "",
      songName: "",
    };
  },
  computed: {
    pageLength: function() {
      return fgr.length;
    },
  },

  methods: {
    breakjson() {
      this.songName = fgr[this.page - 1].Song;
      this.artist =
        fgr[this.page - 1].Artist + " (" + fgr[this.page - 1].Anime + ")";
    },
    floatbtn(dir) {
      if (!(this.page <= 1 && dir < 0)) {
        this.page += dir;
      }
      this.breakjson();
    },
    getLength() {
      this.pageLength = fgr.length();
    },
    sendesh() {
      window.open(
        "http://focusgroupradio.live/radio/8000/admin/metadata?mount=/radio.mp3&mode=updinfo&song=" +
          this.songName +
          "&artist=" +
          this.artist
      );
    },
  },
};
</script>
